// @import url('https://fonts.googleapis.com/css?family=Voltaire');

.valorous-rabbit {
  #world {
    // position: absolute;
    width: 100%;
    height: 100%;
    background-color: #dbe6e6;
    overflow: hidden;
    z-index: -2;
    border-radius: 19px;
    box-shadow: -1px 15px 30px -12px black;
  }

  #gameoverInstructions {
    position: absolute;
    font-family: "Voltaire", sans-serif;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 120px;
    text-align: center;
    color: #ffc5a2;
    opacity: 0;
    left: 50%;
    top: 50%;
    width: 100%;
    transform: translate(-50%, -100%);
    user-select: none;
    transition: all 500ms ease-in-out;

    &.show {
      opacity: 1;
      transform: translate(-50%, -50%);
      transition: all 500ms ease-in-out;
    }
  }

  #dist {
    position: absolute;
    left: 50%;
    top: 50px;
    transform: translate(-50%, 0%);
    user-select: none;
  }

  .label {
    position: relative;
    font-family: "Voltaire", sans-serif;
    text-transform: uppercase;
    color: #ffa873; //100707;
    font-size: 12px;
    letter-spacing: 2px;
    text-align: center;
    margin-bottom: 5px;
  }

  #distValue {
    position: relative;
    text-transform: uppercase;
    color: #dc5f45; //dc5f45;
    font-size: 40px;
    font-family: "Voltaire";
    text-align: center;
  }

  #credits {
    position: absolute;
    width: 100%;
    margin: auto;
    bottom: 0;
    margin-bottom: 20px;
    font-family: "Voltaire", sans-serif;
    color: #544027;
    font-size: 12px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    text-align: center;
    user-select: none;
  }
  #credits a {
    color: #544027;
  }

  #credits a:hover {
    color: #dc5f45;
  }

  #instructions {
    position: absolute;
    width: 100%;
    bottom: 0;
    margin: auto;
    margin-bottom: 50px;
    font-family: "Voltaire", sans-serif;
    color: #dc5f45;
    font-size: 16px;
    letter-spacing: 1px;
    text-transform: uppercase;
    text-align: center;
    user-select: none;
  }
  .lightInstructions {
    color: #5f9042;
  }

  #dist,
  #instructions,
  #credits {
    display: none;
  }
}
